                    <div class="field">
                        <label for="inputDateOfEntry">Date of entry</label>
                        <input type="date" id="inputDateOfEntry"  name="dateOfEntry"  placeholder="DD/MM/YY" autocomplete="off" onkeydown="return (event.keyCode!=13);"> 
